import { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { AppDispatch, RootState } from "../../store";
import { Divider } from "@mui/material";
import Button from "../../components/Button";
import { colors } from "../../styles/colors";
import { useTranslation } from "react-i18next";
import { useNavigate } from "react-router-dom";
import { fetchCategory } from "../../store/categorySlice";
import { CategoryData } from "../../type/categoryType";
import { getSearchHot, getSearchHotMv, search, setSearchHot, setSearchHotMv } from "../../store/videoSlice";
import { Product } from "../../type/videoType";
import VideoVerticalList from "../../components/Video/VideoVerticalList";
import { getFontClass, getImage } from "../../utils/helper";
import Loading from "../../components/Loading";
import VideoMap from "../../components/Video/VideoMap";

interface categoryBtn {
  [id: string]: string;
  name: string;
  png: string;
  url: string;
  name_lang: string;
}

// 註解
const Collections = () => {
  const dispatch: AppDispatch = useDispatch();
  const categoryConfig = useSelector(
    (state: RootState) => state.category.category
  );
  const [loading, setLoading] = useState(true);
  const searchHotKw = useSelector((state: RootState) => state.video.searchHot);
  const searchHotMv = useSelector(
    (state: RootState) => state.video.searchHotMv
  );
  const [category, setCategory] = useState<categoryBtn[]>([]);
  const [section, setSection] = useState<Record<string, any>[]>([]);
  const [searchHotTopFive, setSearchHotTopFive] = useState<Product[]>([]);
  const language = useSelector((state: RootState) => state.init.language);

  // 台灣是 UTC+8，所以用 UTC 計算出當年的 5/1 00:00:00，然後加上 8 小時
  const now = new Date(); // 目前系統時間
  const startTime = new Date('2025-06-01T12:00:00+08:00'); // 台灣時間
  const endTime = new Date('2025-06-30T12:00:00+08:00');
  const isWithinTaiwanTimeRange = now >= startTime && now <= endTime;


  const handlesearchHot = () => {
    const randomIndex = Math.floor(
      Math.random() * ((searchHotKw?.length ?? 0) - 1)
    );
    dispatch(getSearchHotMv({ kw: searchHotKw?.[randomIndex].sh_name }));
  };

  useEffect(() => {
    // console.log(searchHotMv);
    if (searchHotMv) {
      setSearchHotTopFive(searchHotMv.products.slice(0, 5));
      setLoading(false);
    }
  }, [searchHotMv]);

  useEffect(() => {
    // console.log(searchHotMv);
    if (searchHotTopFive.length !== 0) {
      setLoading(false);
    }
  }, [searchHotTopFive]);

  useEffect(() => {
    return () => {
      setLoading(true);
      setSearchHotTopFive([]);
      dispatch(setSearchHot(null));
      dispatch(setSearchHotMv(null));
    }
  }, []);

  useEffect(() => {
    if (searchHotKw) {
      handlesearchHot();
    }
  }, [searchHotKw]);

  useEffect(() => {
    dispatch(fetchCategory());
    dispatch(getSearchHot());
  }, []);

  useEffect(() => {
    console.log("categoryConfig: ", categoryConfig);
    const category = categoryConfig?.filter(
      (item: CategoryData) =>
        item.type !== "category" && item.name !== "精選"
    );
    const categoryBtn = category?.map((item) => ({
      name: item.name,
      png: item.name_en?.match(/^\S+/)?.[0] ?? "", // 假設 icon 對應到 png
      url: item.url,
      name_lang: item[`name_${language}`],
    }));
    setCategory(categoryBtn ?? []);

    const sectionTag = categoryConfig
      ?.filter((item) => item.type == "category")
      .map((item) => ({
        cate: item.name,
        name: item[`name_${language}`]
      }));
    setSection(sectionTag ?? []);
  }, [categoryConfig]);

  const { t } = useTranslation();
  const navigate = useNavigate();

  const categoryClick = (index: number) => {
    if (
      category[index].name === "情趣" ||
      category[index].name_zh_CN === t("情趣")
    ) {
      navigate(`/app/shop`);
    } else if (
      category[index].name === "遊戲" ||
      category[index].name_zh_CN === t("遊戲")
    ) {
      navigate(`/app/game`);
    } else if (
      (category[index].name === "活動" || category[index].name_zh_CN === t("活動")) &&
      isWithinTaiwanTimeRange
    ) {
      navigate(`/app/Event`);
    }else {
      navigate(`/app`, { state: { categoryName: category[index].name } });
    }
  };
  const sectionClick = (cate: string, title: string, index: number) => {
    return () =>
      navigate(`/app/section`, {
        state: { cate: cate, title: title, index: index },
      });
  };

  return (
    <>
      {loading ? (
        <div>
          <Loading/>
        </div>
      ) : (
        <div className="pb-16">
          <div className="flex flex-wrap justify-start">
            {category.map((item, index) => {
              /*if (item.name === "活動" && isBeforeMayFirst){
                return
              }*/
              if (
                (item.name === "視訊" || item.name === t("視訊")) ||
                ((item.name === "活動" || item.name === t("活動")) && !isWithinTaiwanTimeRange)
              ) {
                return (
                  <a
                    href={item.url}
                    target="_blank"
                    key={index}
                    className="flex flex-col items-center w-1/4 px-4 py-4"
                    rel="noreferrer"
                  >
                    {item.png ? (
                      <img
                        src={getImage(item.png)}
                        alt={item.name || "Default"}
                        className="w-10 h-10"
                      />
                    ) : (
                      <div className="w-10 h-10"></div>
                    )}
                    <span className="text-lg">{item.name_lang}</span>
                  </a>
                );
              }
              return (
                <div
                  key={index}
                  className="flex flex-col items-center w-1/4 px-4 py-4"
                  onClick={() => categoryClick(index)}
                >
                  {item.png ? (
                    <img
                      src={getImage(item.png)}
                      alt={item.name || "Default"}
                      className="w-10 h-10"
                    />
                  ) : (
                    <div className="w-10 h-10"></div>
                  )}
                  <span className={getFontClass(language)}>{item.name_lang}</span>
                </div>
              );
            })}
          </div>
          <Divider variant="middle" sx={{ bgcolor: colors.primary.active }} />
          <div className={`${getFontClass(language)} p-4`}>
            <h2 className="text-xl text-center my-2">{t("專區")}</h2>
            <div className="flex flex-wrap justify-start">
              {section.map((item, index) => (
                <div key={index} className="my-2 mx-2">
                  <Button
                    className="active button-rounded"
                    onClick={sectionClick(item.cate, item.name, index)}
                  >
                    {item.name}
                  </Button>
                </div>
              ))}
            </div>
          </div>
          <Divider variant="middle" sx={{ bgcolor: colors.primary.active }} />
          <div className={`${getFontClass(language)} p-4`}>
            <h2 className="text-xl text-center my-2">{t("熱門影片")}</h2>
            <div className="flex flex-wrap justify-start">
              <VideoMap data={searchHotTopFive} />
            </div>
          </div>
        </div>
      )}
    </>
  );
};
export default Collections;
